/**
 * Reset Password Page - ×”×–× ×ª ×¡×™×¡××” ×—×“×©×”
 *
 * âœ… CODE-003: Migrated to TypeScript
 */

import React, { useState, useEffect, type FC, type FormEvent, type ChangeEvent } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { FileText, Lock, Eye, EyeOff, Loader2, CheckCircle, AlertCircle } from 'lucide-react';
import { toast } from 'sonner';
import { apiPost } from '../utils/api';
import { logger } from '../utils/logger';

// ===========================
// Types
// ===========================

interface FormData {
  password: string;
  confirmPassword: string;
}

interface FormErrors {
  password?: string | null;
  confirmPassword?: string | null;
}

// ===========================
// Component
// ===========================

const ResetPasswordPage: FC = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const token = searchParams.get('token');

  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [formData, setFormData] = useState<FormData>({
    password: '',
    confirmPassword: ''
  });
  const [isLoading, setIsLoading] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [errors, setErrors] = useState<FormErrors>({});
  const [tokenError, setTokenError] = useState('');

  useEffect(() => {
    if (!token) {
      setTokenError('×§×™×©×•×¨ ×œ× ×ª×§×™×Ÿ. × × ×œ×‘×§×© ×§×™×©×•×¨ ××™×¤×•×¡ ×—×“×©.');
    }
  }, [token]);

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {};

    if (!formData.password) {
      newErrors.password = '× × ×œ×”×–×™×Ÿ ×¡×™×¡××” ×—×“×©×”';
    } else if (formData.password.length < 6) {
      newErrors.password = '×”×¡×™×¡××” ×—×™×™×‘×ª ×œ×”×›×™×œ ×œ×¤×—×•×ª 6 ×ª×•×•×™×';
    }

    if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = '×”×¡×™×¡×××•×ª ×œ× ×ª×•×××•×ª';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e?: FormEvent<HTMLFormElement>): Promise<void> => {
    e?.preventDefault();

    if (!validateForm()) {
      return;
    }

    setIsLoading(true);

    try {
      logger.log('ğŸ” Resetting password...');
      await apiPost('/api/auth/reset-password', {
        token,
        password: formData.password
      }, { showErrorToast: true });

      setIsSuccess(true);
      toast.success('×”×¡×™×¡××” ×©×•× ×ª×” ×‘×”×¦×œ×—×”!');

    } catch (err) {
      logger.error('âŒ Password reset error:', err);
      const errorMessage = err instanceof Error ? err.message : '';

      if (errorMessage.includes('expired') || errorMessage.includes('×¤×’ ×ª×•×§×£')) {
        setTokenError('×”×§×™×©×•×¨ ×¤×’ ×ª×•×§×£. × × ×œ×‘×§×© ×§×™×©×•×¨ ××™×¤×•×¡ ×—×“×©.');
      } else if (errorMessage.includes('invalid') || errorMessage.includes('×œ× ×ª×§×™×Ÿ')) {
        setTokenError('×§×™×©×•×¨ ×œ× ×ª×§×™×Ÿ. × × ×œ×‘×§×© ×§×™×©×•×¨ ××™×¤×•×¡ ×—×“×©.');
      } else {
        toast.error(errorMessage || '×©×’×™××” ×‘××™×¤×•×¡ ×”×¡×™×¡××”. × ×¡×” ×©×•×‘.');
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleChange = (field: keyof FormData) => (e: ChangeEvent<HTMLInputElement>): void => {
    setFormData(prev => ({ ...prev, [field]: e.target.value }));
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: null }));
    }
  };

  // Error state - invalid/expired token
  if (tokenError) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-900 via-slate-900 to-purple-900 flex items-center justify-center p-4">
        <div className="w-full max-w-md">
          <div className="text-center mb-8">
            <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-br from-red-500 to-orange-600 rounded-2xl mb-4">
              <AlertCircle className="text-white" size={32} />
            </div>
            <h1 className="text-3xl font-bold text-white mb-2">
              ×§×™×©×•×¨ ×œ× ×ª×§×™×Ÿ
            </h1>
            <p className="text-red-300">{tokenError}</p>
          </div>

          <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-8 border border-white/20">
            <button
              onClick={() => navigate('/forgot-password')}
              className="w-full bg-gradient-to-r from-blue-500 to-purple-600 text-white py-3 rounded-lg font-semibold hover:from-blue-600 hover:to-purple-700 transition-all"
            >
              ×‘×§×© ×§×™×©×•×¨ ××™×¤×•×¡ ×—×“×©
            </button>

            <button
              onClick={() => navigate('/login')}
              className="w-full mt-4 text-blue-300 hover:text-white transition-colors text-sm"
            >
              ×—×–×¨×” ×œ×“×£ ×”×›× ×™×¡×”
            </button>
          </div>
        </div>
      </div>
    );
  }

  // Success state
  if (isSuccess) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-900 via-slate-900 to-purple-900 flex items-center justify-center p-4">
        <div className="w-full max-w-md">
          <div className="text-center mb-8">
            <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-br from-green-500 to-emerald-600 rounded-2xl mb-4">
              <CheckCircle className="text-white" size={32} />
            </div>
            <h1 className="text-3xl font-bold text-white mb-2">
              ×”×¡×™×¡××” ×©×•× ×ª×” ×‘×”×¦×œ×—×”!
            </h1>
            <p className="text-blue-200">
              ×›×¢×ª ×ª×•×›×œ ×œ×”×ª×—×‘×¨ ×¢× ×”×¡×™×¡××” ×”×—×“×©×”
            </p>
          </div>

          <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-8 border border-white/20">
            <button
              onClick={() => navigate('/login')}
              className="w-full bg-gradient-to-r from-green-500 to-emerald-600 text-white py-3 rounded-lg font-semibold hover:from-green-600 hover:to-emerald-700 transition-all"
            >
              ×”×ª×—×‘×¨ ×œ××¢×¨×›×ª
            </button>
          </div>
        </div>
      </div>
    );
  }

  // Form state
  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-900 via-slate-900 to-purple-900 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-br from-blue-500 to-purple-600 rounded-2xl mb-4">
            <FileText className="text-white" size={32} />
          </div>
          <h1 className="text-3xl font-bold text-white mb-2">
            ×”×–×Ÿ ×¡×™×¡××” ×—×“×©×”
          </h1>
          <p className="text-blue-200">
            ×‘×—×¨ ×¡×™×¡××” ×—×–×§×” ×©×ª×•×›×œ ×œ×–×›×•×¨
          </p>
        </div>

        <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-8 border border-white/20">
          <form onSubmit={handleSubmit} className="space-y-5">
            {/* New Password */}
            <div>
              <label className="block text-sm font-semibold text-white mb-2" htmlFor="password">
                ×¡×™×¡××” ×—×“×©×”
              </label>
              <div className="relative">
                <Lock className="absolute right-3 top-3 text-blue-300" size={20} />
                <input
                  id="password"
                  type={showPassword ? 'text' : 'password'}
                  value={formData.password}
                  onChange={handleChange('password')}
                  className={`w-full pr-10 pl-12 py-3 bg-white/10 border ${errors.password ? 'border-red-500' : 'border-white/20'} rounded-lg text-white placeholder-blue-200`}
                  placeholder="×œ×¤×—×•×ª 6 ×ª×•×•×™×"
                  autoFocus
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute left-3 top-3 text-blue-300"
                >
                  {showPassword ? <EyeOff size={20} /> : <Eye size={20} />}
                </button>
              </div>
              {errors.password && <p className="text-red-400 text-sm mt-1">{errors.password}</p>}
            </div>

            {/* Confirm Password */}
            <div>
              <label className="block text-sm font-semibold text-white mb-2" htmlFor="confirmPassword">
                ××™××•×ª ×¡×™×¡××”
              </label>
              <div className="relative">
                <Lock className="absolute right-3 top-3 text-blue-300" size={20} />
                <input
                  id="confirmPassword"
                  type={showConfirmPassword ? 'text' : 'password'}
                  value={formData.confirmPassword}
                  onChange={handleChange('confirmPassword')}
                  className={`w-full pr-10 pl-12 py-3 bg-white/10 border ${errors.confirmPassword ? 'border-red-500' : 'border-white/20'} rounded-lg text-white placeholder-blue-200`}
                  placeholder="×”×–×Ÿ ×©×•×‘ ××ª ×”×¡×™×¡××”"
                />
                <button
                  type="button"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  className="absolute left-3 top-3 text-blue-300"
                >
                  {showConfirmPassword ? <EyeOff size={20} /> : <Eye size={20} />}
                </button>
              </div>
              {errors.confirmPassword && <p className="text-red-400 text-sm mt-1">{errors.confirmPassword}</p>}
            </div>

            <button
              type="submit"
              disabled={isLoading}
              className="w-full bg-gradient-to-r from-green-500 to-emerald-600 text-white py-3 rounded-lg font-semibold hover:from-green-600 hover:to-emerald-700 transition-all disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
            >
              {isLoading ? (
                <>
                  <Loader2 className="animate-spin" size={20} />
                  <span>××©× ×” ×¡×™×¡××”...</span>
                </>
              ) : (
                '×©× ×” ×¡×™×¡××”'
              )}
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ResetPasswordPage;
